// 3. ORGANISM [ 3.1. CARDS ] #########################################################################################

// A. GLOBAL STYLES +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// A.1. GENERAL CARDS -------------------------------------------------------------------------------------------------

// A.1.1. BOOTSTRAP CARDS ---------------------------------

.card { border: 0;
  .m-card-image { margin: 0; position: relative; overflow: hidden; display: block; }
  .m-card-body {
    .a-card-header { margin: 0; padding: 0; font-size: 14px; color: $white; text-shadow: 0 0 5px $black; }
    .a-card-text, p { margin: 0; padding: 0; font-size: 12px; color: $white; text-shadow: 0 0 5px $black;
      &.truncated { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; transition: 0.5s; }
    }
  }
  .m-card-footer {
    .a-btn { min-width: auto; }
  }
}

// A.1.1. END ---------------------------------------------

// A.1.2. BASE CARD SETTINGS ------------------------------

.o-card { overflow: hidden; position: relative;
  &.stacked {
    .m-card-image { margin: 0;
      img { width: 100%; height: auto; }
    }
    .m-card-body {}
    .m-card-footer {
      .a-btn { min-width: auto; }
    }
  }


  // A.1.1.1. HOVER CARD ----------------------------------

  $aspect-ratio:   56%;

  &.hover-card { transition: 0.3s ease; position: relative;

    .m-card-image { @include aspect-ratio (100%, ($aspect-ratio));
      .a-image, img { @include middle-align(); position: absolute; width: 100%; height: auto; transition: 0.3s ease; }
    }

    .m-card-body {
      //@include position(absolute, 0);
      padding: $base;
      background: rgba(0,0,0,0.8);
      transition: 0.3s ease;
      transform: translateY(0%);

      //.a-card-header { position: absolute; top: 45px; left: $base; }
      .a-card-text, p {
        //position: absolute;
        //top: 75px;
        //left: $base;
        //right: $base;
        line-height: 14px;
        height: 14px;
        // TRUNCATE TEXT
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transition: 0.5s;
      }

    }

    &:hover { transition: 0.3s ease;
      .m-card-image {
        img,.a-image { scale: 200; transition: 0.3s ease; }
      }
      .m-card-body { top: 0; height: 50%; background: rgba(0,0,0,0.9); transition: 0.3s ease; transform: translateY(-50%);
        .a-card-header { margin: 0; transition: 0.8s; /*top: 80%;*/ }
        .a-card-text, p { white-space: normal; transition: 0.6s; height: auto; /*top: 100%;*/ }
      }
    }

  }

  // A.1.1.1. END -----------------------------------------

}

// A.1.1. END ---------------------------------------------

// A.1. END -----------------------------------------------------------------------------------------------------------

